<template>
  <div id="app">
  <transition name="fade">
    <router-view></router-view>
  </transition>
  </div>
</template>

<script>
export default {
  name: 'app',
  data: function () {
    return {
      iserr: true,
      islogin: false,
      message: 'Hello Vue.js!!'
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir','Microsoft JhengHei', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 50px;
}
.fade-enter-active {
  transition: opacity 1s
}
.fade-leave,
.fade-leave-active {
  display: none;
}
.fade-enter {
