
<template>
<div id="mainpage" class="mainpage">
  <div class="closebtn ps-top ps-right"  @click="delcookie"></div>
  <h2 class="ml-left"> {{ h2 }} </h2>
  <div class="content">
    <transition name="fade">
      <img class="image" :src="imgsrc" v-show="getimg"/>
    </transition>
    <span v-show="!getimg"> {{ msg }} </span>
  </div>
  <br/>
  <div class="btn" @click="onSubmit"> {{ submit }} </div><br/>
</div>
</template>

<script>
const API_URL = 'https://yesno.wtf/api'

export default {
  name: 'Login',
  data: function () {
    return {
      h2: 'YES or NO',
      msg: 'YES or NO',
      getimg: false,
      imgsrc: '',
      submit: '???'
    }
  },
  created () {
    this.h2 += ' - [ ' + this.getCookie('session') + ' ]'
  },
  methods: {
    onSubmit () {
      this.getimg = false
      this.msg = '猜看看'
      console.log('onSubmit')
      var i = this
      var xhr = new XMLHttpRequest()
      xhr.open('GET', API_URL)
      xhr.onload = function () {
        var res = JSON.parse(xhr.responseText)
        console.log(res)
        i.imgsrc = res.image
      }
      xhr.send()
      document.querySelector('img').addEventListener('load', function () {
        console.log('addEventListener')
        i.getimg = true
      })
    },
    delcookie () {
      console.log('delcookie')
      this.delCookie('session')
      this.$router.push('/')
    }
  },
  watch: {
    account: function (x) {
      this.account = x.toString().replace(/[^a-zA-Z0-9]/g, '')
    }
  }
}
</script>

<style scoped>
* {
  box-sizing: border-box;
  font-family: Consolas,'Microsoft JhengHei', sans-serif;
}
.mainpage {
  position: relative;
  margin: 0 auto auto auto;
  padding: 0px;
  width: 600px;
  height: auto;
  text-align: center;
  background-color: #ACF;
  border: 2px solid pink;
  border-radius: 5px 5px 5px 5px;
}
.content {
  position: relative;
  margin: 0px auto;
  padding: 0px;
  width: 550px;
  min-height: 200px;
  text-align: center;
  vertical-align: middle;
  line-height: 200px;
  color: white;
  background: black;
  border: 1px solid #0F0;
  border-radius: 4px;
}
.image {
  display: block;
  max-width: 100%;
  max-height: 100%;
  width: 100%;
}
.closebtn {
  width: 20px;
  height: 20px;
  background: #AAA;
  border: 1px solid white;
  border-radius: 50%;
  cursor: pointer;
  transition: .15s;
}
.closebtn:hover {
  transform: rotate(90deg);
}
.closebtn::before,
.closebtn::after {
  content:'';
  position: absolute;
  top: 10px;
  left: 1px;
  width: 18px;
  border-top: 2px solid white;
  transform: translate(-1px,-2px) rotate(-135deg);
}
.closebtn::after {
  transform: translate(-1px,-2px) rotate(135deg);
}
.btn {
  position: relative;
  margin: auto;
  width: 260px;
  height: 34px;
  line-height: 34px;
  color: #fff;
  text-shadow: rgba(100,0,0,1) 0 0 3px;
  background-color: #006ac6;
  border: 1px solid #ccc;
  border-radius: 4px;
  cursor: pointer;
  transition: .01s;
  transition-timing-function: ease-in;
}
.btn-dark {
  background: -webkit-linear-gradient(-60deg, black 40%, coral 60%);
  background:    -moz-linear-gradient(-60deg, black 40%, coral 60%);
  background:     -ms-linear-gradient(-60deg, black 40%, coral 60%);
  background:         linear-gradient(150deg, black 40%, #008A00 60%);
}
.btn:hover {
  background-color: #005ab6;
  border: 1px solid #999;
}
.btn:active {
  background-color: rgba(0, 0, 0, 1);
  border: 0px solid #000;
  transform: translate(1px,1px);
}
.fade-enter-active {
  transition: opacity .5s
}
.fade-leave,
.fade-leave-active {
  display: none;
}
.fade-enter {
  opacity: 0;
}
.ml-left {
  margin: 40px 0px 40px 25px;
  text-align: left;
}
.ps-top {
  top: 5px;
}
.ps-bottom {
  bottom: 5px;
}
.ps-right {
  position: absolute;
  right: 5px;
}
</style>



