
<template>
<div id="loginbox" class="loginbox">
  <div class="closebtn ps-top ps-right"  @click="closebtn"></div>
  <h2 class="ml-left"> {{ l.h2 }} </h2>
  <input class="form-control" :placeholder="l.ac" maxlength="12" type="text" v-model="account"/><br/>
  <input class="form-control" :placeholder="l.pw" maxlength="12" type="password" v-model="password"/><br/>
  <br/>
  <div class="btn" @click="onSubmit"> {{ l.submit }} </div><br/>
  <transition name="msg-fade">
  <p :class="{pmsg: true, info: !loginiserr, error: loginiserr}" v-show="showmsg"> {{ loginmsg }} </p>
  </transition>
  <select class='ps-bottom ps-right' v-model="selected" @change="onChange">
    <option v-for="item in languagelist" :value="item.tt"> {{ item.text }} </option>
  </select>
</div>
</template>

<script>
import axios from 'axios'

var LANG = {'h2': 'title', 'ac': 'Account3', 'pw': 'Password3', 'submit': 'Push3', 'mistake': 'mistook', 'nullin': 'something is null'}
var LANGEN = {'h2': 'Loginsystem', 'ac': 'Account', 'pw': 'Password', 'submit': 'Login', 'mistake': 'Mistake'}
var LANGTC = {'h2': '內容管理系統', 'ac': '帳號', 'pw': '密碼', 'submit': '登入', 'mistake': '錯誤!'}
var LANGJPN = {'h2': 'Jp1', 'ac': 'Jp2', 'pw': 'Jp3', 'submit': 'Jp4', 'mistake': 'Jp5'}

export default {
  name: 'Login',
  data: function () {
    return {
      l: LANG,
      account: '',
      password: '',
      loginmsg: '',
      showmsg: false,
      loginiserr: false,
      selected: 'EN',
      languagelist: [
        { text: 'English', tt: 'EN' },
        { text: '繁體中文 (Traditional Chinese)', tt: 'TC' },
        { text: 'Francais (French)', tt: 'FRA' },
        { text: 'Deutsch (German)', tt: 'GEM' },
        { text: '日本語 (Japanese)', tt: 'JPN' },
        { text: 'български (Bulgarian)', tt: 'BG' },
        { text: 'Češka (Czech)', tt: 'CZH' },
        { text: 'Danske (Danish)', tt: 'DAN' },
        { text: 'Nederlands (Dutch)', tt: 'NLD' },
        { text: 'Ελληνικός (Greek)', tt: 'EL' },
        { text: 'עברית (Hebrew)', tt: 'HEB' },
        { text: 'Magyar (Hungarian)', tt: 'HUN' },
        { text: 'Italiano (Italian)', tt: 'ITA' },
        { text: 'Bahasa Indonesia (Indonesian)', tt: 'ID' },
        { text: 'Norske (Norwegian)', tt: 'NO' },
        { text: 'Lietuvos (Lithuanian)', tt: 'LT' },
        { text: 'فارسی (Persian)', tt: 'PRS' },
        { text: 'Polski (Polish)', tt: 'POL' },
        { text: 'Português (Portuguese)', tt: 'PYU' },
        { text: 'Limba română (Romanian)', tt: 'RO' },
        { text: 'Русский язык (Russian)', tt: 'RUS' },
        { text: 'Српски језик (Serbian)', tt: 'SRL' },
        { text: '简体中文 (Simplified Chinese)', tt: 'SC' },
        { text: 'Slovenščina (Slovenian)', tt: 'Sl' },
        { text: 'Slovenčina (Slovakian)', tt: 'SK' },
        { text: 'Español (Spanish)', tt: 'SPN' },
        { text: 'ภาษาไทย (Thai)', tt: 'TAI' },
        { text: 'Türkçe (Turkish)', tt: 'TR' },
        { text: 'Svenska (Swedish)', tt: 'SV' },
        { text: 'Suomi (Finnish)', tt: 'FI' },
        { text: 'اللغة العربية (Arabic)', tt: 'ARB' }
      ]
    }
  },
  methods: {
    onChange () {
      console.log('onChange')
      switch (this.selected) {
        case 'EN':
          for (let i = 0; i < Object.keys(LANG).length; i++) {
            LANG[Object.keys(LANG)[i]] = LANGEN[Object.keys(LANG)[i]]
          }
          break
        case 'TC':
          for (let i = 0; i < Object.keys(LANG).length; i++) {
            LANG[Object.keys(LANG)[i]] = LANGTC[Object.keys(LANG)[i]]
          }
          break
        case 'JPN':
          for (let i = 0; i < Object.keys(LANG).length; i++) {
            LANG[Object.keys(LANG)[i]] = LANGJPN[Object.keys(LANG)[i]]
          }
          break
      }
    },
    closebtn () {
      console.log('closebtn')
      this.$router.go(-1)
    },
    onSubmit () {
      console.log('onSubmit')
      if (this.account !== '' && this.password !== '') {
        this.tologin()
      } else {
        this.showmsg = true
        this.loginiserr = true
        this.loginmsg = this.l.nullin
      }
    },
    tologin () {
      console.log('tologin')
      var i = this
      axios.post('http://192.168.4.3/verify.php', {
        vacc: i.account,
        vpsd: i.password,
        vlangid: i.selected
      })
      .then(function (response) {
        var verify = response.data.state === '200'
        i.showmsg = true
        i.loginiserr = !verify
        i.loginmsg = response.data.msg
        if (verify) {
          i.setCookie('session', i.account, 10)
          i.$router.push('/dist/main')
        }
      })
    }
  },
  watch: {
    account: function (x) {
      this.account = x.toString().replace(/[^a-zA-Z0-9]/g, '')
    },
    showmsg: function (x) {
      var i = this
      if (x) {
        setTimeout(function () {
          i.showmsg = false
        }, 3000)
      }
    },
    selected: function (x) {
      console.log(x)
    }
  }
}
</script>

<style scoped>
* {
  box-sizing: border-box;
  font-family: Consolas,'Microsoft JhengHei', sans-serif;
}
.loginbox {
  position: relative;
  margin: 0 auto auto auto;
  padding: 0px;
  height: 400px;
  text-align: center;
  background-color: #ACF;
  border: 2px solid pink;
  border-radius: 5px 5px 5px 5px;
}
@media screen and (max-width: 639px) {
/*畫面小於寬度*/
  .loginbox {
  width: 95%;
  }
}
@media screen and (min-width:640px) {
/*畫面大於寬度*/
  .loginbox {
  width: 360px;
  }
}
.form-control {
  margin: 5px 10px 5px 10px;
  width: 260px;
  height: 34px;
  padding: 6px 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
}
.closebtn {
  width: 20px;
  height: 20px;
  background: #AAA;
  border: 1px solid white;
  border-radius: 50%;
  cursor: pointer;
  transition: .15s;
}
.closebtn:hover {
  transform: rotate(90deg);
}
.closebtn::before,
.closebtn::after {
  content:'';
  position: absolute;
  top: 10px;
  left: 1px;
  width: 18px;
  border-top: 2px solid white;
  transform: translate(-1px,-2px) rotate(-135deg);
}
.closebtn::after {
  transform: translate(-1px,-2px) rotate(135deg);
}
.pmsg {
  margin: 10px 10px 10px 10px;
}
.btn {
  position: relative;
  margin: auto;
  width: 260px;
  height: 34px;
  line-height: 34px;
  color: #fff;
  text-shadow: rgba(100,0,0,1) 0 0 3px;
  background-color: #006ac6;
  border: 1px solid #ccc;
  border-radius: 4px;
  cursor: pointer;
  transition: .01s;
  transition-timing-function: ease-in;
}
.btn-dark {
  background: -webkit-linear-gradient(-60deg, black 40%, coral 60%);
  background:    -moz-linear-gradient(-60deg, black 40%, coral 60%);
  background:     -ms-linear-gradient(-60deg, black 40%, coral 60%);
  background:         linear-gradient(150deg, black 40%, #008A00 60%);
}
.btn:hover {
  background-color: #005ab6;
  border: 1px solid #999;
}
.btn:active {
  background-color: rgba(0, 0, 0, 1);
  border: 0px solid #000;
  transform: translate(1px,1px);
}
.msg-fade-leave,
.msg-fade-leave-active {
  transition: opacity .5s
}
.msg-fade-leave-to {
  opacity: 0;
}
.info {
  color: #0F0;
}
.error {
  color: #F00;
}
.ml-left {
  margin: 40px 0px 40px 25px;
  text-align: left;
}
.ps-top {
  top: 5px;
}
.ps-bottom {
  bottom: 5px;
}
.ps-right {
  position: absolute;
  right: 5px;
}
</style>



